import Component, { tracked } from '@glimmer/component';

export default class ThemedQuestion extends Component {

  questionAnswered = false;

  didInsertElement() {
    window.setTimeout(() => {
      if (!this.questionAnswered) {
        alert('TIMEOUT!');
        this.args.clearQuestion();
      }
    }, 20000)
  }

  selectAnswer(answer) {
    if (answer === this.args.question.correct) {
      alert('CORRECT ;)');
    } else {
      alert('INCORRECT :(');
    }
    this.questionAnswered = true;
    this.args.clearQuestion();
  }
};
