const questions = [];

questions.push({ title: 'Which is an irregular verb', answers: ['Clean', 'Paint', 'Hit', 'Finish'], correct: 2})
questions.push({ title: 'Best synonym of coax', answers: ['Persuade', 'Command', 'Inspect', 'Search'], correct: 0})
questions.push({ title: 'If I had her phone number, I\'d ring her', answers: ['I have her number', 'I don\'t have her number', 'I had her number.', 'I didn\'t have her number'], correct: 1})
questions.push({ title: 'I remember _________ the Queen in London.', answers: ['meet', 'to meet', 'meeting', 'to meeting'], correct: 2})
questions.push({ title: 'Photos is a ______ noun', answers: ['common', 'concrete', 'abstract', 'proper'], correct: 0})
questions.push({ title: 'Hanna is a _____ noun', answers: ['common', 'concrete', 'abstract', 'proper'], correct: 1});
questions.push({ title: 'We ______ that game, but we went on to win the series.', answers: ['win', 'winned', 'won', 'wan'], correct: 2});
questions.push({ title: 'We _______ the perfect spot for a picnic: a patch of grass by a peaceful stream.', answers: ['chosen', 'choosed', 'chose', 'choose'], correct: 2});
questions.push({ title: 'Choose the best personal pronound for this sentence: Christina, ________ the best!', answers: ['Thomas is', 'you\'re', 'ice cream is', 'Santa Claus is'], correct: 1});
questions.push({ title: 'What\'s the adjective?', answers: ['Carrots', 'With', 'Cut', 'Sharp'], correct: 3});
questions.push({ title: 'I do believe that stuffed moneky is _______', answers: ['hi\'s', 'his', 'his\'', 'him\'s'], correct: 1});
questions.push({ title: 'The _______ spurs jingled as he strode into the salooon', answers: ['cowboys\'', 'cowboy\'s', 'cowboys', 'boycow'], correct: 1});
questions.push({ title: 'You ______ broken the high jump record if you had gotten a better running start', answers: ['cou\'ldve', 'could\'ve', 'couldve', 'should have'], correct: 1});
questions.push({ title: 'If you ______ able to go to the store after work, I can pick up some groceries for you.', answers: ['arent', 'aren\'t', 'are\'nt', 'will'], correct: 1});
questions.push({ title: '_____ got a bad feeling about this', answers: ['Ive', 'Iv\'e', 'I\'ve', 'Was yoda'], correct: 2});

export default function() {
  const index = Math.floor(Math.random() * questions.length);
  const question = questions[index];
  questions.splice(index, 1);
  return question;
}
